@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	background-color: black;
	color: white;
	font-family: "Roboto", sans-serif;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	min-height: 100vh;
	padding: 2rem;
}

h1 {
	margin: 2rem 0;
	letter-spacing: 10px
}
h1 span {
	color: #646464;
	font-size: 70%;
}

.board-container {
	position: relative;
	padding-bottom: 100%;
	margin-bottom: 1.5rem;

	display: -webkit-flex;

	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.board {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;
	margin: auto;

	/* background: white; */
	background: black;
	display: grid;
	grid-template: repeat(3, auto) / repeat(3, auto);
	grid-gap: 0.3rem;
}

.squares {
	background: #000000;
	border: none;
	font-size: 5rem;
	cursor: pointer;
	outline: none;
	min-width: 100px;
	min-height: 100px;
	padding: 0;

	border: 1px solid rgb(255, 255, 255, 0.2);
}

.X {
	color: rgb(255, 179, 179);
}
.O {
	color: rgb(185, 255, 220);
}

.info-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

h3 {
	padding: 1rem;
}

li {
	list-style: none;
}

.info-wrapper button {
	background: #272727;
	color: white;
	border: none;
	outline: none;
	cursor: pointer;
	padding: 0.5rem;
	/* margin: 0.5rem 1rem; */
}

.settings {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	margin-bottom: 2rem;
}
.settings button {
	background: #272727;
	color: white;
	border: none;
	outline: none;
	cursor: pointer;
	padding:  1rem 2rem;
	/* margin: 0.5rem 1rem; */
	width: 100%;
}

